import thunk from 'redux-thunk'
import { createStore, combineReducers, applyMiddleware, compose } from 'redux'
import setAuthorizationToken from '../utils/AuthorizationToken'
import { setCurrentUser, authToken } from '../actions/authActions'
import auth from '../reducers/auth'
import load from '../reducers/load'
export default async function configureStore() {
	const store=createStore(combineReducers({auth,load}),compose(applyMiddleware(thunk),window.devToolsExtension?window.devToolsExtension():f=>f))
	const checkToken = async () =>{
		const referralToken=localStorage.referral_token
		const token= await authToken(referralToken)
		setAuthorizationToken(token)
		if(token){
			store.dispatch(setCurrentUser(token))
		}else{
			if(referralToken){
				localStorage.removeItem('referral_token')
			}
		}
	}
	await checkToken()
	return store
}