import React from 'react'
import { Button, Card, CardBody, FormGroup, Form, Input, Col } from 'reactstrap'
class Login extends React.Component {
  render() {
    return (
      <Col lg='5' md='7'>
        <Card className='bg-secondary shadow border-0'>
          <CardBody className='px-lg-5 py-lg-5'>
            <div className='text-center text-muted mb-4'>
            <h1 className='underline-text'>LOGIN</h1>
            </div>
            <Form role='form'>
              <FormGroup className='mb-3'>
                <label><b>Username</b></label>
                <Input placeholder='Username' name='username' type='text' autoComplete='username' />
              </FormGroup>
              <FormGroup>
                <label><b>Password</b></label>
                <Input placeholder='Password' name='password' type='password' autoComplete='password' />
              </FormGroup>
              <div className='custom-control custom-control-alternative custom-checkbox'>
                <input className='custom-control-input' id='customCheckLogin' type='checkbox' />
                <label className='custom-control-label' htmlFor='customCheckLogin'>
                  <span className='text-muted'>Remember me</span>
                </label>
              </div>
              <div className='text-center'>
                <Button className='btn-block my-4' color='primary' type='button'>login</Button>
              </div>
            </Form>
          </CardBody>
        </Card>
      </Col>
    )
  }
}
export default Login