import React from 'react'
import { NavLink as NavLinkRRD, Link } from 'react-router-dom'
import { PropTypes } from 'prop-types'
import {
  Collapse,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  Row,
  Col
} from 'reactstrap';
class Sidebar extends React.Component {
  state = {
    collapseOpen: false
  };
  constructor(props) {
    super(props);
    this.activeRoute.bind(this);
  }
  activeRoute(routeName) {
    return this.props.location.pathname.indexOf(routeName) > -1 ? 'active' : '';
  }
  toggleCollapse = () => {
    this.setState({
      collapseOpen: !this.state.collapseOpen
    });
  };
  closeCollapse = () => {
    this.setState({
      collapseOpen: false
    });
  };
  createLinks = routes => {
    return routes.map((prop, key) => {
      return (
        <NavItem key={key}>
          <NavLink to={prop.layout + prop.path} tag={NavLinkRRD} onClick={this.closeCollapse} activeClassName='active'>
            <i className={prop.icon} />
            <b>{prop.name}</b>
          </NavLink>
        </NavItem>
      );
    });
  };
  render() {
    const { routes, logo } = this.props;
    let navbarBrandProps;
    if (logo && logo.innerLink) {
      navbarBrandProps = {
        to: logo.innerLink,
        tag: Link
      };
    } else if (logo && logo.outterLink) {
      navbarBrandProps = {
        href: logo.outterLink,
        target: '_blank'
      };
    }
    return (
      <Navbar className='navbar-vertical fixed-left' expand='md'>
        <Container fluid>
          <button className='navbar-toggler' type='button' onClick={this.toggleCollapse}>
            <span className='navbar-toggler-icon' />
          </button>
          {logo ? (
            <NavbarBrand className='pt-3' {...navbarBrandProps}>
              <img alt={logo.imgAlt} className='navbar-brand-img' src={logo.imgSrc} /></NavbarBrand>) : null}
          <Nav className="align-items-center d-md-none"></Nav>
          <Collapse navbar isOpen={this.state.collapseOpen}>
            <div className='navbar-collapse-header d-md-none'>
              <Row>
                {logo ? (
                  <Col className='collapse-brand text-center' xs={{ size: 6, offset: 3 }}>
                    {logo.innerLink ? (
                      <Link to={logo.innerLink}><img alt={logo.imgAlt} src={logo.imgDarkSrc} /></Link>
                    ) : (
                        <a href={logo.outterLink}>
                          <img alt={logo.imgAlt} src={logo.imgSrc} />
                        </a>
                      )}
                  </Col>
                ) : null}
                <Col className='collapse-close' xs='3'>
                  <button className='navbar-toggler' type='button' onClick={this.toggleCollapse}>
                    <span /><span />
                  </button>
                </Col>
              </Row>
            </div>
            <Nav navbar>{this.createLinks(routes)}</Nav>
          </Collapse>
        </Container>
      </Navbar>
    );
  }
}
Sidebar.defaultProps = {
  routes: [{}]
};
Sidebar.propTypes = {
  routes: PropTypes.arrayOf(PropTypes.object),
  logo: PropTypes.shape({
    innerLink: PropTypes.string,
    outterLink: PropTypes.string,
    imgSrc: PropTypes.string.isRequired,
    imgAlt: PropTypes.string.isRequired,
    imgDarkSrc: PropTypes.string.isRequired
  })
};
export default Sidebar;
