import * as CryptoJS from 'crypto-js';
  export default {
		async signatureJWT(value) {
			return await CryptoJS.HmacSHA256(value,process.env.REACT_APP_JWT_KEY).toString(await CryptoJS.enc.Base64).replace(/[+]/g,"-").replace(/\//g,"_").replace(/=/g,"")
		},
		async decodeBase64(value) {
			return await CryptoJS.enc.Base64.parse(value).toString(await CryptoJS.enc.Utf8)
		},
		async encodeBase64Text(text) {
			return await CryptoJS.enc.Base64.stringify(await CryptoJS.enc.Utf8.parse(JSON.stringify(text))).replace(/=/g,"")
		},
		async encodeBase64(data) {
			return await CryptoJS.enc.Base64.stringify(await CryptoJS.enc.Utf8.parse(JSON.stringify(data))).replace(/[+]/g,"-").replace(/\//g,"_").replace(/=/g,"")
		},
		async decode(token) {
			try {
				if (!token || token.length ===0) {
					return null;
				}
				const data = token.split('.')
				if (data.length !== 3) {
					return false;
				}
				if(await this.signatureJWT(data[0]+'.'+data[1])!==data[2]){
					return false;
				}
				return JSON.parse(await this.decodeBase64(data[1]));
			} catch (err) {
				return false;
			}
		},
		async encode(data) {
			try {
				const base64Header = await this.encodeBase64({
					'typ': 'JWT',
					'alg': 'HS256'
				})
				const base64Payload = await this.encodeBase64(data)
				return base64Header + '.' + base64Payload + '.' + await this.signatureJWT(base64Header + '.' + base64Payload)
			} catch (err) {
				return null;
			}
		}
}