import { LOADING_ACTIVE,LOADING_INACTIVE } from '../actions/types'
const initialState = {
  active: true
}
export default (state = initialState, action = {}) => {
  switch(action.type) {
    case LOADING_ACTIVE:
      return {
        active: true
      }
    case LOADING_INACTIVE:
      return {
        active: false
      }
    default: return state
  }
}
