import React from 'react'
import { connect } from 'react-redux'
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom'
import RequireAuth from '../utils/RequireAuth'
import AuthLogin from '../utils/AuthLogin'
import AdminLayout from '../layouts/Admin.js'
import AuthLayout from '../layouts/Auth.js'
import { LOADING_ACTIVE, LOADING_INACTIVE } from '../actions/types'
class App extends React.Component{
	UNSAFE_componentWillMount() {
		this.props.setLoad(false)
	}
	UNSAFE_componentWillUpdate(nextProps) {
		if(nextProps.load.active){
			nextProps.setLoad(false)
		}
	}
	render(){
		return (
			<BrowserRouter>
			<Switch>
			<Route path='/admin' component={RequireAuth(AdminLayout)}/>
			<Route path='/auth' component={AuthLogin(AuthLayout)}/>
			<Redirect from='/' to='/auth'/>
			</Switch>
			</BrowserRouter>
			)
	}
}
function mapStateToProps(state){
	return state
}
function mapDispatchToProps(dispatch){
	return {
		setLoad:(active)=>{
			if(active){
				dispatch({
					type:LOADING_ACTIVE
				})
			}else{
				dispatch({
					type:LOADING_INACTIVE
				})
			}
		}
	}
}
export default connect(mapStateToProps,mapDispatchToProps)(App);
